      h2 1.1 Gua para la elaboracin del Plan de Accin Comunitario
    p(data-aos="fade-down") La gua para la elaboracin del plan de accin es una herramienta fundamental de planificacin participativa que orienta la ruta de trabajo hacia el cumplimiento del sueo colectivo. A travs de ella, la comunidad define actividades, responsables y tiempos de ejecucin, garantizando que los actores y lderes comunitarios participen activamente en la construccin de Mi Producto OVOP a partir de los recursos disponibles en el territorio.
    p.mb-5(data-aos="fade-down") En el enfoque OVOP Colombia, el Plan de Accin Comunitario se estructura en tres componentes principales:
