              h5.text-center Mecanismos de gestin, basados en procesos de autogestin y cogestin que permitan dinamizar el territorio y avanzar hacia el sueo colectivo.
    p.mb-5(data-aos="fade-down") Planear bajo el enfoque OVOP implica activar los principios de autogestin, creatividad y participacin comunitaria. Este proceso busca que las comunidades desarrollen su potencial colectivo, asumiendo responsabilidades compartidas entre pares y con las instituciones del territorio. De este modo, se promueve la bsqueda conjunta de soluciones a situaciones socialmente relevantes.
    .row.justify-content-center.mb-5
