            .col-lg-11.col-12
              p(data-aos="fade-down") La planificacin OVOP tambin integra la perspectiva de <em>backcasting</em>, un enfoque que inicia desde el futuro deseado. La comunidad proyecta su visin o sueo y, desde all, retrocede al presente para identificar las acciones necesarias que permitan alcanzarlo. La pregunta central es:
            .col-lg-7.col-12
