    p.mb-5(data-aos="fade-down") Este enfoque facilita que la comunidad reflexione sobre las acciones, recursos, actores e instituciones requeridas para avanzar hacia la visin planteada, identificando relaciones causaefecto que fundamentan el proceso de planificacin.
    .row.justify-content-center.mb-5
