          p.mb-0(data-aos="fade-down") El diseo del plan de accin tambin contribuye al fortalecimiento de las comunidades de prctica, entendidas como grupos que comparten ideas, intercambian conocimientos, generan soluciones e impulsan procesos de innovacin en torno a un propsito comn.
