    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 1.2 Gua de Negocio Colectivo para la gestin de Mi Producto OVOP
    .row.justify-content-center.mb-4
      .col-lg-8.order-lg-1.order-2
        p(data-aos="fade-down") Esta gua presenta las pautas para la creacin de un Negocio Colectivo, con potencial de desarrollo en un territorio definido. Los pasos aqu propuestos se basan en la experiencia adquirida en la primera fase del proyecto OVOP Colombia y se sustentan en procesos econmicos orientados a mejorar las condiciones de vida de las comunidades.
        .cajon.cajon.C02.color-primario.p-4.mb-3
          p.mb-0(data-aos="fade-down") Un Negocio Colectivo se entiende como un sistema de integracin que rene a individuos, unidades familiares y organizaciones alrededor de un emprendimiento comn. Este modelo permite juntar recursos, capacidades y talentos para ampliar los resultados, fortalecer habilidades comunitarias y promover el valor nico del producto a travs del <em>marketing</em> territorial.
      .col-lg-4.order-lg-2.order-1.mb-3.col-md-5.col-7
        figure(data-aos="fade-up") 
          img(src='@/assets/curso/temas/tema1/img12.png', alt='Imagen decorativa')
    .row.justify-content-center.mb-5(data-aos="flip-up")
      .col-lg-10.col-12
