            img(src='@/assets/curso/temas/tema1/img13.svg', alt='En la figura 2 se presenta el modelo de Negocio Colectivo, el cual contiene elementos que se integran y dan como resultado la meta deseada.')
            figcaption Nota. Adaptado de la Gua 7 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema1/img14.svg', alt='En la figura 2 se presenta el modelo de Negocio Colectivo, el cual contiene elementos que se integran y dan como resultado la meta deseada.')
          figcaption Nota. Adaptado de la Gua 7 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .row.justify-content-center
