                  p(data-aos="fade-down") Un ejemplo emblemtico es Tuchn, Crdoba, donde el 90 % de sus 57.361 habitantes trabaja en la cadena de valor de la caa flecha: 73,3 % en tejido, 13,6 % en confeccin, 6,8 % en sombrerera y 2,3 % en cestera (DANE, 2024).
    p.mb-5(data-aos="fade-down") Estos tesoros o recursos se clasifican en:
