          h3.mb-0.ms-3 Aclaracin de conceptos: Negocio individual vs Negocio Colectivo
    .col-lg-2.col-md-5.col-auto
      .titulo-sexto.color-acento-contenido01.etiqueta01
        h5 Negocio individual
    p.mb-5(data-aos="fade-down") Corresponde a un emprendimiento con un nico dueo responsable de la gestin, los beneficios y los riesgos. Su objetivo principal consiste en minimizar costos y aumentar ingresos para maximizar ganancias.
    .row.justify-content-center.mb-5(data-aos="flip-up")
      .col-lg-10.col-12
