      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema1/img23.svg', alt='En la figura 4 se representa un modelo de negocio individual, en el cual la minimizacin de costos junto con el aumento de ingresos da como resultado la maximizacin de la ganancia.')
          figcaption Nota. Tomado de la Gua 7 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    .col-lg-2.col-md-5.col-auto
      .titulo-sexto.color-acento-contenido01.etiqueta01
        h5 Negocio Colectivo
    p.mb-5(data-aos="fade-down") Se integra por varias personas, familias u organizaciones que asumen responsabilidades compartidas mediante un enfoque colaborativo. Su propsito es mejorar el valor del producto, incrementar la capacidad de venta, abrir nuevos mercados y fortalecer los beneficios colectivos e individuales.
    .row.justify-content-center.mb-5
      .col-lg-8.col-12
        .row.justify-content-center
          .col-lg-6.col-12.order-lg-2.order-2
            h5.mb-4 Pueden integrar un Negocio Colectivo quienes:
            ol.lista-ol--cuadro.lista-ol--separador.mb-0
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 1
                | Posean un producto comn.
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 2
                | Tengan claridad del territorio donde operan.
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 3
                | Participen en la cadena de valor del producto.
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 4
                | Tengan disposicin para trabajar en equipo.
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 5
                | Busquen dar valor a su producto.
              li.mb-2
                .lista-ol--cuadro__vineta
                  span.text-bold 6
                | Estn abiertos a esquemas colaborativos.
          .col-lg-6.col-8.col-md-7.order-lg-1.order-1
            figure
              img(src='@/assets/curso/temas/tema1/img24.svg', alt='Imagen decorativa')
