          h5 Figura 5.
          span <em>Representacin de un Negocio Colectivo</em>
      .row.justify-content-center
        .col-10
          figure.desktop
            img(src='@/assets/curso/temas/tema1/img25.svg', alt='En la figura 5 se representa un modelo de Negocio Colectivo, en el cual la mejora del valor junto con el aumento de los ingresos genera la maximizacin de los beneficios.')
            figcaption Nota. Tomado de la Gua 7 OVOP Colombia (SENA, JICA, MinCIT, 2020).
      .col-12
        figure.movil
          img(src='@/assets/curso/temas/tema1/img26.svg', alt='En la figura 5 se representa un modelo de Negocio Colectivo, en el cual la mejora del valor junto con el aumento de los ingresos genera la maximizacin de los beneficios.')
          figcaption Nota. Tomado de la Gua 7 OVOP Colombia (SENA, JICA, MinCIT, 2020).
    p.mb-5(data-aos="fade-down") Cuando un territorio selecciona Mi Producto OVOP e identifica a las unidades familiares, organizaciones e individuos que hacen parte de su cadena de valor y que estn interesados en establecer relaciones de trabajo colaborativo para impulsar el crecimiento econmico de su comunidad, es necesario definir un modelo de negocio que permita cumplir con los objetivos principales de dicho trabajo colectivo.
    p(data-aos="fade-down") La estructura del Modelo de Negocio Colectivo para la gestin de Mi Producto OVOP se expresa en la metodologa CANVAS. Este modelo permite representar, de manera grfica, un modelo de negocio a travs de nueve mdulos que abarcan cuatro reas principales: clientes, oferta, infraestructura y viabilidad econmica. Para el enfoque OVOP se emplea Lean Canvas, una adaptacin que ofrece una estructura ms simple, pero que permite construir un modelo de negocio ms completo que el obtenido mediante el modelo PICTO.
    p.mb-5(data-aos="fade-down") Lean Canvas es una herramienta utilizada en la fase inicial de creacin de una empresa. Su propsito es validar de forma clara y visual el modelo de negocio, identificar debilidades y fortalezas y aplicar las mejoras necesarias para aumentar las posibilidades de obtener beneficios (Founderz, s. f).
    .row.justify-content-center.mb-3.p-5(data-aos="flip-up")
      .col-lg-9.position-relative
        .avatar
          figure
            img(src="@/assets/curso/temas/tema1/img27.svg", alt="Imagen decorativa").img260
        .crd.crd--avatarHorizontal.crd--first.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/temas/tema1/img28.svg", alt="Imagen decorativa").img100
            .col
              p Se busca aumentar los ingresos de Mi Producto mediante una gestin comercial eficiente que permita abrir nuevos canales de venta y establecer precios competitivos.
        .crd.crd--avatarHorizontal.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/temas/tema1/img29.svg", alt="Imagen decorativa").img100
            .col
              p Tambin se pretende maximizar los beneficios a travs de la mejora en los procesos de produccin, aumentando la rentabilidad en cada etapa de la cadena de valor y cumpliendo con los estndares de calidad, diseo y acceso a nuevos mercados.
        .crd.crd--avatarHorizontal.crd--last.py-4
          .row.align-items-center
            .col-auto
              figure
                img(src="@/assets/curso/temas/tema1/img30.svg", alt="Imagen decorativa").img100
            .col
              p Adems, se busca posicionar Mi Pueblo a partir de Mi Producto, estableciendo una relacin directa entre la imagen del territorio (nombre, valores, historia, tesoros, etc.) y las caractersticas del producto, con el fin de fortalecer tanto el valor del producto como el del entorno, generando una Marca Territorial basada en el trabajo colectivo.
    p(data-aos="fade-down") La estructura del Modelo de Negocio Colectivo para la gestin de Mi Producto OVOP se expresa en la metodologa CANVAS. Este modelo permite representar, de manera grfica, un modelo de negocio a travs de nueve mdulos que abarcan cuatro reas principales: clientes, oferta, infraestructura y viabilidad econmica. Para el enfoque OVOP se emplea Lean Canvas, una adaptacin que ofrece una estructura ms simple, pero que permite construir un modelo de negocio ms completo que el obtenido mediante el modelo PICTO.
    p.mb-5(data-aos="fade-down") Lean Canvas es una herramienta utilizada en la fase inicial de creacin de una empresa. Su propsito es validar de forma clara y visual el modelo de negocio, identificar debilidades y fortalezas y aplicar las mejoras necesarias para aumentar las posibilidades de obtener beneficios (Founderz, s. f).
