    p.mb-5(data-aos="fade-down") Una vez estructurado el modelo de Negocio Colectivo mediante Lean Canvas, es necesario avanzar hacia la planificacin de mediano plazo y la definicin de los pasos que permitirn convertir este modelo en una realidad dentro del territorio. Este proceso incluye tanto la organizacin estratgica para los prximos aos como la formalizacin del Negocio Colectivo, garantizando que la iniciativa OVOP cuente con bases slidas para su sostenibilidad y crecimiento.
    TabsC.color-acento-botones.mb-5(data-aos="flip-up")
      .py-3.py-md-4(titulo="Plan de gestin a mediano plazo")
        .row.justify-content-center.p-4
          .col-lg-5.col-10.order-lg-1.order-1.mb-lg-0.mb-4
            figure(data-aos="fade-right")
              img(src='@/assets/curso/temas/tema1/img31.jpg', alt='Imagen decorativa')
          .col-lg-7.order-lg-2.order-2
            p Corresponde a un instrumento concreto orientado a desarrollar los conceptos superiores de gestin, como la misin y la visin. Se recomienda un perodo de tres a cinco aos. Su elaboracin implica analizar la situacin actual, revisar detalladamente las direcciones estratgicas posibles, seleccionar la ms adecuada y definir los planes de accin clave.
            p Por lo general, este plan incluye una estrategia para toda la organizacin, una estrategia por divisin y estrategias por funcin. En OVOP Colombia, los planes especficos, adems de la estrategia general, se integran en el plan de accin detallado y se complementan con diferentes apoyos del Comit OVOP.
      .py-3.py-md-4(titulo="Materializacin del Negocio Colectivo")
        .row.justify-content-center.p-4
          .col-lg-5.col-10.order-lg-1.order-1.mb-lg-0.mb-4
            figure(data-aos="fade-right")
              img(src='@/assets/curso/temas/tema1/img32.jpg', alt='Imagen decorativa')
          .col-lg-7.order-lg-2.order-2
            p En el marco del Negocio Colectivo, la formalizacin consiste en establecer una organizacin o empresa colectiva con reconocimiento legal, con el fin de ampliar su capacidad de operacin comercial y fortalecer su competitividad en el mercado. Esta formalizacin garantiza transparencia ante el Estado.
            p Colombia cuenta con legislacin vigente que permite a los ciudadanos constituir empresas bajo parmetros legales y con el acompaamiento de entidades como las Cmaras de Comercio, Secretaras Econmicas, Superintendencias y Ministerios.
            p.mb-0 Las dos figuras principales para formalizar una empresa ante las Cmaras de Comercio son:
            ul.lista-ul.mb-0(data-aos="fade-down")
              li.mb-0
                i.lista-ul__vineta
                p.mb-0 <b>Persona natural</b>: el ciudadano que, de manera personal, asume la titularidad legal y la responsabilidad principal de la empresa.
              li.mb-0
                i.lista-ul__vineta
                p.mb-0 <b>Persona jurdica</b>: persona ficticia, capaz de ejercer derechos y contraer obligaciones, y de ser representada judicial y extrajudicialmente (Cmara de Comercio, 2017).
    p.mb-5(data-aos="fade-down") Para orientar esta fase, se presenta la ficha tcnica correspondiente al Negocio Colectivo para la gestin de Mi Producto. Esta herramienta describe las acciones necesarias alineadas con el enfoque OVOP.
