      h2 1.3 Gua para el anlisis de actores
    .bloque-texto-g.bloque-texto-g--inverso.color-primario.p-3.p-sm-4.p-md-5.mb-5(data-aos="flip-up")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema1/img33.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0(data-aos="fade-down") Para continuar con el desarrollo del modelo de Negocio Colectivo, resulta pertinente avanzar hacia un anlisis ms profundo que permita comprender las dinmicas del territorio y los actores que influyen en el proceso de consolidacin de la iniciativa. En esta etapa, el anlisis de actores ofrece insumos conceptuales y metodolgicos esenciales para identificar, clasificar y seleccionar a los actores clave vinculados al enfoque OVOP Colombia. Su aplicacin permite reconocer quines participan en la iniciativa, las acciones que desarrollan, las relaciones que mantienen y el nivel de influencia que ejercen sobre la formulacin, implementacin y sostenibilidad del proyecto.
    p(data-aos="fade-down") Este anlisis se convierte en un componente estratgico que facilita la articulacin territorial y la toma de decisiones informadas. Reconocer que los actores sociales (personas, grupos u organizaciones) poseen intereses y niveles de poder diferenciados permite anticipar riesgos, fortalecer alianzas y promover procesos de complementariedad que contribuyan a la viabilidad del Negocio Colectivo.
    p.mb-0(data-aos="fade-down") Entre los aspectos ms relevantes que aporta esta fase se encuentran:
    .row.justify-content-center.BG03.align-items-center.pt-5.pb-5
      .col-lg-11.col-12
        .row.justify-content-center
          .col-lg-9.col-12.order-lg-2.order-2
            ol.lista-ol--cuadro.lista-ol--separador.mb-0
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 1
                | La identificacin de actores pblicos, privados, comunitarios, religiosos o lderes locales.
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 2
                | La clasificacin de los actores segn su rol, inters, responsabilidad o capacidad de influencia.
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 3
                | El anlisis de la dinmica relacional entre los actores y su impacto sobre la iniciativa.
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 4
                | La identificacin de oportunidades de articulacin y cooperacin.
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 5
                | La valoracin de actores clave que pueden impulsar o limitar el proceso.
              li
                .lista-ol--cuadro__vineta
                  span.text-bold 6
                | La orientacin metodolgica para el taller de anlisis de actores y la consolidacin de la ficha tcnica correspondiente.
          .col-lg-3.col-8.col-md-7.order-lg-1.order-1
            figure
              img(src='@/assets/curso/temas/tema1/img34.svg', alt='Imagen decorativa')
    p(data-aos="fade-down") Con esta comprensin del entorno y de los actores involucrados, se establece una base slida para introducir el Plan de gestin a mediano plazo y la Materializacin del Negocio Colectivo, donde se definen las acciones estratgicas necesarias para fortalecer y formalizar la iniciativa en el territorio.
    p.mb-5(data-aos="fade-down") Para orientar esta fase, se presenta la ficha tcnica correspondiente al anlisis de actores. Esta herramienta describe las acciones necesarias alineadas con el enfoque OVOP.
