    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 1.4 Gua de Estrategia de Marca Territorial
    p(data-aos="fade-down") La Gua de Estrategia de Marca Territorial, ofrece orientaciones conceptuales y metodolgicas para comprender la Marca Territorial como una herramienta clave para promover cooperacin orgnica entre instituciones, comunidades y actores del desarrollo local. Su propsito es fortalecer la articulacin departamental y orientar la realizacin de talleres que permitan consolidar una estrategia de marca coherente con los principios del enfoque OVOP Colombia.
    p.mb-5(data-aos="fade-down") En esta fase resulta fundamental clarificar algunos conceptos esenciales:
    .tarjeta.tarjeta--BG04.p-md-5.p-4.mb-5(data-aos="flip-up")
      SlyderA(tipo="b")
        .row.justify-content-center.p-4
          .col-lg-5.order-lg-1.order-2
            h4 Marca
            p De acuerdo con la Asociacin Americana de <em>Marketing</em> (AMA), una marca corresponde a un nombre, trmino, seal, smbolo o diseo que identifica productos y servicios y los diferencia de la competencia. Representa una promesa del vendedor y una evaluacin del comprador.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema1/img35.jpg', alt='Imagen decorativa')
        .row.justify-content-center.p-4
          .col-lg-5.order-lg-1.order-2
            h4 Marca Territorial
            p Comprende dos componentes que se integran para proyectar una identidad diferenciada:
            ul.lista-ul--color
              li 
                i.fas.fa-check
                p.mb-0 La imagen del territorio como representacin global de la regin.
              li 
                i.fas.fa-check
                p.mb-0 Los productos y servicios locales que se construyen a partir de recursos propios del territorio.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema1/img36.jpg', alt='Imagen decorativa')
        .row.justify-content-center.p-4
          .col-lg-5.order-lg-1.order-2
            h4 Estrategia de marca
            p Se entiende como el proceso mediante el cual se otorga valor de marca a los productos o servicios, logrando diferenciacin, posicionamiento y fortalecimiento en la toma de decisiones del cliente.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema1/img37.jpg', alt='Imagen decorativa')
        .row.justify-content-center.p-4
          .col-lg-5.order-lg-1.order-2
            h4 Estrategia de Marca Territorial
            p Constituye un proceso orientado a revitalizar el territorio mediante la creacin de marca de los productos/servicios locales y de la imagen territorial. Segn el Ministerio de Economa, Comercio e Industria de Japn, esta estrategia busca atraer recursos externos a partir de un ciclo virtuoso entre imagen territorial y oferta productiva local.
          .col-lg-7.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            figure
              img(src='@/assets/curso/temas/tema1/img38.jpg', alt='Imagen decorativa')
    p.mb-5(data-aos="fade-down") La implementacin de la estrategia se desarrolla en tres etapas principales:
    .row.mb-5
      .col-lg-4.col-lg.mb-5.mb-lg-0(data-aos="flip-up")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img39.svg' alt='AvatarTop')
          .tarjeta.tarjeta--C02(style= "width: 100%")
            .p-4(data-aos="fade-down")
              h5.text-center Construccin
      .col-lg-4.col-lg.mb-5.mb-lg-0(data-aos="flip-up")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img40.svg' alt='AvatarTop')
          .tarjeta.tarjeta--C02(style= "width: 100%")
            .p-4(data-aos="fade-down")
              h5.text-center Aprovechamiento
      .col-lg-4.col-lg.mb-5.mb-lg-0(data-aos="flip-up")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img41.svg' alt='AvatarTop')
          .tarjeta.tarjeta--C02(style= "width: 100%")
            .p-4(data-aos="fade-down")
              h5.text-center Administracin
    p(data-aos="fade-down") Estas etapas se articulan con los dos ejes centrales de OVOP Colombia: la participacin social y el fortalecimiento competitivo de los productos.
    p.mb-5(data-aos="fade-down") Para desarrollar el taller asociado a esta estrategia, se requieren dos condiciones previas:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul" data-aos="zoom-in")
      .row.justify-content-center(titulo="Nivel operativo")
        .col-lg-6.order-lg-1.order-2
          p.mb-0 Contar con avances en el proceso formativo OVOP y haber completado la segunda fase de la Ruta Metodolgica, adems de disponer de:
