    p.mb-5(data-aos="fade-down") El desarrollo del taller contempla, de forma general, cinco pasos contenidos en la Ruta Metodolgica OVOP:
    .BG05.mb-5(data-aos="flip-up")
      .row.justify-content-center.align-items-center(data-aos="fade-down")
