    p.mb-5(data-aos="fade-down") Con el fin de orientar esta fase, se presenta a continuacin la ficha tcnica correspondiente a la Estrategia de Marca Territorial, la cual detalla las acciones necesarias alineadas con el enfoque OVOP.
