    separador
    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 1.5 Gua de participacin y desarrollo comunitario
    .row.justify-content-center.mb-4
      .col-lg-7.order-lg-1.order-2
        p(data-aos="fade-down") Esta gua tiene como propsito ofrecer insumos conceptuales y metodolgicos que facilitan la participacin social y la inclusin comunitaria dentro del enfoque OVOP. Su orientacin permite fortalecer la cohesin social, promover la apropiacin del territorio y consolidar procesos de desarrollo local con la participacin activa de diversos grupos de la comunidad.
        .cajon.cajon.C02.color-primario.p-4.mb-3
          p.mb-0(data-aos="fade-down") La propuesta parte del reconocimiento de las desigualdades presentes entre zonas urbanas y rurales y de la necesidad de impulsar estrategias de desarrollo incluyentes. En este sentido, la participacin e inclusin social se convierten en pilares fundamentales para evitar que la iniciativa OVOP se limite nicamente a la competitividad del producto, asegurando que todos los actores del territorio se beneficien del proceso.
        p.mb-0(data-aos="fade-down") Desde esta perspectiva, el desarrollo comunitario se apoya en varios principios esenciales:
      .col-lg-5.order-lg-2.order-1.mb-3.col-md-5.col-7
        figure(data-aos="fade-up") 
          img(src='@/assets/curso/temas/tema1/img50.png', alt='Imagen decorativa')
    .row.justify-content-center.mb-4
      .col-lg-10.col-12
        .cajon.cajon.C03.p-4.mb-0
          .row.justify-content-center
            .col-12
              ul.lista-ul--color.mb-0
                li 
                  i.fas.fa-angle-right
                  | Fortalecer la cohesin y la confianza entre los miembros de la comunidad.
                li 
                  i.fas.fa-angle-right
                  | Vincular a grupos vulnerables como mujeres cabeza de hogar, jvenes, adultos mayores, comunidades indgenas, personas con discapacidad o vctimas del conflicto.
                li 
                  i.fas.fa-angle-right
                  | Incentivar el liderazgo local a travs de la formacin de lderes comunitarios OVOP.
                li.mb-0
                  i.fas.fa-angle-right
                  | Promover el dilogo entre distintos sectores y actores del territorio.
    .row.justify-content-center.mb-5
      .col-lg-10.col-12
        p.mb-0(data-aos="fade-down") 
    .row.mb-5(data-aos="flip-up")
      .col-md-6.col-lg.tarjeta--tabla.p-4
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema1/img51.svg' alt='AvatarTop')
        p.text-center.text-bold(data-aos="fade-down") Identificar y reconocer a los actores sociales vinculados al proceso.
      .col-md-6.col-lg.tarjeta--tabla.p-4
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema1/img52.svg' alt='AvatarTop')
        p.text-center.text-bold(data-aos="fade-down") Construir y validar el sueo colectivo de la comunidad.
      .col-md-6.col-lg.tarjeta--tabla.p-4
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema1/img53.svg' alt='AvatarTop')
        p.text-center.text-bold(data-aos="fade-down") Analizar necesidades, oportunidades y prioridades del territorio.
      .col-md-6.col-lg.tarjeta--tabla.p-4
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema1/img54.svg' alt='AvatarTop')
        p.text-center.text-bold(data-aos="fade-down") Formular un plan de accin participativo acorde con la realidad local.
      .col-md-6.col-lg.tarjeta--tabla.p-4
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema1/img55.svg' alt='AvatarTop')
        p.text-center.text-bold(data-aos="fade-down") Consolidar compromisos que permitan avanzar hacia la transformacin territorial.
    p(data-aos="fade-down") Una vez finalizado el taller, se recomienda sistematizar la informacin recopilada, elaborar un informe ejecutivo, evaluar la actividad para mejorar futuras sesiones y compartir los resultados con los participantes, garantizando transparencia y retroalimentacin constante.
    p.mb-5(data-aos="fade-down") Esta gua complementa la Ruta Metodolgica OVOP y brinda las orientaciones necesarias para asegurar que el proceso comunitario sea inclusivo, representativo y sostenible.
