    #t_1_6.titulo-segundo.color-acento-contenido(data-aos="flip-up")
      h2 1.6 Gua de <em>Marketing</em>
    p.mb-5(data-aos="fade-down") La Gua de <em>Marketing</em> de OVOP Colombia proporciona una ruta metodolgica para disear estrategias comerciales que impulsen los productos locales (Mi Producto) dentro del territorio, fortaleciendo su identidad y competitividad.
    .row.justify-content-center.mb-0
      .col-12
        .row.justify-content-center
          .col-lg-10.col-12.order-lg-2.order-2
            .tarjeta.tarjeta--C06.p-2.mb-5
              .row.justify-content-center.align-items-center
                .col-lg-2.col-8.col-md-7.order-lg-1.order-1.mb-3
                  figure
                    img(src='@/assets/curso/temas/tema1/img56.svg', alt='Imagen decorativa')
                .col-lg-9.col-12.order-lg-2.order-2
                  p(data-aos="fade-down") Objetivo principal es orientar a facilitadores y coordinadores para aplicar herramientas de <em>marketing</em> adaptadas al contexto del desarrollo local, de modo que la promocin del producto sirva como palanca para el crecimiento econmico, social y cultural.
    p.mb-5(data-aos="fade-down") La gua plantea un ciclo de <em>marketing</em> particular denominado <b>R-STP-MM-I-C</b> que consta de los siguientes pasos:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul" data-aos="zoom-in")
      .row.justify-content-center(titulo="Investigacin (R)")
        .col-lg-6.order-lg-1.order-2
          ul.lista-ul--color.mb-0(data-aos="fade-down") 
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Identificacin de recursos locales y productos preliminares.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Anlisis FODA para diagnosticar fortalezas, debilidades, oportunidades y amenazas.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Evaluacin del entorno mediante el anlisis de las 3C: clientes, competencia y contexto.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/temas/tema1/img57.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Segmentacin, seleccin del pblico objetivo y posicionamiento (STP)")
        .col-lg-6.order-lg-1.order-2
