              p.mb-0 Divisin del mercado en segmentos segn caractersticas demogrficas, psicogrficas y de actitud.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Seleccin de los segmentos ms relevantes para Mi Producto.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Definicin del posicionamiento, es decir, de la percepcin que se quiere generar en el pblico objetivo.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/temas/tema1/img58.jpg', alt="Imagen decorativa")
      .row.justify-content-center(titulo="Mezcla de Mercadotecnia (<em>Marketing</em> Mix - MM)")
        .col-lg-6.order-lg-1.order-2
          ul.lista-ul--color.mb-0(data-aos="fade-down") 
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Producto: caractersticas, diseo, calidad y valor cultural.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Precio: estrategias para garantizar competitividad y sostenibilidad.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Plaza (distribucin): canales adecuados para llegar al cliente, considerando tambin el alcance local y regional.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Promocin: acciones de comunicacin y visibilidad, tanto presencial como digital.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
