      .row.justify-content-center(titulo="Implementacin y control (I-C)")
        .col-lg-6.order-lg-1.order-2
          ul.lista-ul--color.mb-0(data-aos="fade-down") 
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Diseo de un plan de negocios que integra las decisiones anteriores.
            li.mb-0
              i.lista-ul__vineta
              p.mb-0 Seguimiento y evaluacin de las acciones de <em>marketing</em> para ajustar estrategias segn resultados.
        .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
          figure
            img(src='@/assets/curso/temas/tema1/img60.jpg', alt="Imagen decorativa")
    .row.justify-content-center.BG06.align-items-center.pt-5.pb-5
      .col-11
        p(data-aos="fade-down") La gua sugiere desarrollar un plan de negocios que incluya los anlisis de mercado, segmentacin, mezcla de <em>marketing</em> y control, permitiendo validar la viabilidad comercial del producto.
        p(data-aos="fade-down") Se recomienda que las comunidades utilicen el <em>marketing</em> no solo para vender, sino tambin para fortalecer su identidad territorial y generar orgullo local. Adems, la estrategia debe contemplar un enfoque sostenible y colaborativo, aprovechando tanto los recursos locales como redes de apoyo e institucionales.
        p.mb-0(data-aos="fade-down") Para orientar esta fase, se presenta la ficha tcnica correspondiente a la Gua de <em>marketing</em>. Esta herramienta describe las acciones necesarias alineadas con el enfoque OVOP.
