      h2 1.7 Gua de planificacin participativa basada en el marco lgico
    .row.justify-content-center.mb-5
      .col-lg-7.order-lg-2.order-2
        .cajon.cajon.C02.color-primario.p-4.mb-3
          p.mb-0(data-aos="fade-down") La Gua de planificacin participativa basada en el marco lgico orienta a coordinadores municipales, facilitadores departamentales y miembros de los comits locales OVOP en la realizacin de talleres de Metodologa de Construccin Participativa (MCP). Su propsito es facilitar la construccin colectiva del futuro del territorio en torno al producto principal seleccionado, dentro de las iniciativas que integran el enfoque OVOP Colombia.
        p.mb-4(data-aos="fade-down") Es importante precisar que este ejercicio se diferencia de la formulacin del Plan de Accin OVOP bsico descrito en la fase tres de planificacin. La MCP se centra en la reflexin estratgica participativa y en la identificacin de problemas, oportunidades y soluciones que fortalecen la visin de desarrollo local.
        p.mb-0(data-aos="fade-down") La metodologa aporta mltiples beneficios para el territorio y los actores involucrados. Entre los ms relevantes se encuentran:
      .col-lg-5.order-lg-1.order-1.mb-3.col-md-5.col-7
        figure(data-aos="fade-up") 
